.app-header{display: flex;
    border: 3px solid #ccc;

}
img{
    width: 150px;
    height: 150px;
}
li{
    display: inline-block;
    padding: 80px;
    
}
.restaurant-card{
    border: 1px solid #ccc;
    padding: 10px;
    margin: 10px;
    width: 200px;
    text-align: center;
}
.restaurant-card:hover{
    background-color: #f0f0f0;
    border: 2px solid #000;
    cursor: pointer;

}
.rest-container{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
}
#footer1{
    display: flex;
    border: 3px solid #ccc;
    text-align: center;
   justify-content: center;

}